html {
    font-size: 62.5%;
    box-sizing: border-box;
}

html, body {
    height: 100%;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

#page {
    position: relative;
    min-width: 320px;
    min-height: 100%;
    max-width: 100%;
    overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, img, ol, ul, li, figure {
    margin: 0;
    padding: 0;
}

h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
}

img {
    width: 100%;
    vertical-align: middle;
}

ul, li {
    list-style: none;
}

a {
    display: block;
    color: inherit;
    line-height: 1;
    text-decoration: none;
    cursor: pointer;
}

a:active, a:hover {
    outline: 0;
}

input:focus {
    outline: none;
    border: none;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
    min-width: 0;
}